* {
  margin: 0;
  padding: 0;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.main {
  width: 100%;
  height: 100vh;
  }

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.content {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  align-items: center;
  color: white;
}


.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.3);
}

p {
  font-size: 1.6rem;
}

h1 {
  font-size: 6rem;
}

input {
  padding: .7rem 1.5rem;
  margin-top: 20%;
  font-size: 1.2rem;
  border-radius: 5px;
  border: 1px solid rgba(255,255,255, 0.8);
  background: rgba(255,255,255, 0.1);
  color: white;
}

::placeholder{
  color: white;
}

.bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 50%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  border-color: rgba(255,255,255, 0.2);
}

.top {
margin-top: 5%;

}

.bold {
  font-size: 35px;
  margin-top: 5%;
}